import FlowRenderer from "Container/ChatFlowDashboard/FlowRenderer/FlowRenderer";
import FlowControls from "Components/ChatFlowDashboard/FlowControls/FlowControls";
import FlowDrawer from "Container/ChatFlowDashboard/FlowDrawer/FlowDrawer";
import {
  FlowDrawerProvider,
  FlowDataProvider,
  FlowDialogProvider,
  FlowServicesProvider,
  FlowNodeServicesProvider,
  FlowHistoryProvider,
} from "../contexts/ChatFlowDashboard";
import { ReactFlowProvider } from "react-flow-renderer";

const ChatFlowDashboard = () => {
  return (
    <FlowHistoryProvider>
      <FlowDialogProvider>
        <FlowDrawerProvider>
          <FlowDataProvider>
            <FlowNodeServicesProvider>
              <FlowServicesProvider>
                <ReactFlowProvider>
                  <div className="d-flex">
                    <FlowRenderer />
                    <FlowControls />
                    <FlowDrawer />
                  </div>
                </ReactFlowProvider>
              </FlowServicesProvider>
            </FlowNodeServicesProvider>
          </FlowDataProvider>
        </FlowDrawerProvider>
      </FlowDialogProvider>
    </FlowHistoryProvider>
  );
};

export default ChatFlowDashboard;
